import React from 'react';

import lady1 from '../../assets/images/lady-1.png';
import lady1_2x from '../../assets/images/lady-1@2x.png';

// TODO: change faq items text, add links
const faqItems = [
  {
    id: 1,
    title: 'What is The Sandbox?',
    paragraphs: ['The Sandbox is a virtual world built on the Ethereum blockchain, where players can build, own, and monetize their gaming experiences.'],
  },
  {
    id: 2,
    title: 'Apes of Space VX mint price?',
    paragraphs: ['Public - 0.1 ETH', 'Whilelist - 0.08 ETH'],
  },
  {
    id: 3,
    title: 'Does owning an Ape give commercial use rights?',
    paragraphs: ['Owning a Ape of Space allows you to use the image for any commercial purpose.'],
  },
  {
    id: 4,
    title: 'My transaction fee is a few thousand dollars! What happened?',
    paragraphs: ['When trying to push an impossible transaction, Metamask will show a ridiculously high fee.'],
  },
  {
    id: 5,
    title: 'OpenSea link?',
    paragraphs: ['AoS Collection: <a class="link" href="https://opensea.io/collection/apesofspace-official">https://opensea.io/collection/apesofspace-official</a>', 'rarity.tools: <a class="link" href="https://rarity.tools/apesofspace-official/">https://rarity.tools/apesofspace-official/</a>'],
  },
  {
    id: 6,
    title: 'What are the contract addresses?',
    paragraphs: ['AoS Contract: <a class="link" href="https://etherscan.io/address/0xf6e2c197e96b63b982ca639262cdf0a5f296d528">https://etherscan.io/address/0xf6e2c197e96b63b982ca639262cdf0a5f296d528</a>', 'LAoS Contract: <a class="link" href="https://etherscan.io/address/0x92eb1aa966a6a9ed2ffe18d9abfa2805cddc81b2">https://etherscan.io/address/0x92eb1aa966a6a9ed2ffe18d9abfa2805cddc81b2</a>'],
  },
  {
    id: 7,
    title: 'Any other questions? Reach out!',
    paragraphs: ['Reach out on any of the following channels:', 'Twitter: <a class="link" href="https://twitter.com/ApesofSpace_nft">https://twitter.com/ApesofSpace_nft</a>', 'Discord: <a class="link" href="https://discord.com/invite/rRrnHCQsvn">https://discord.com/invite/rRrnHCQsvn</a>'],
  },
];

function Faq() {
  const handleAccordeonClick = (e) => {
    if (e.target.matches('a')) {
      return;
    }

    const target = e.target.closest('.faq-accordeon__item');

    if (target) {
      target.classList.toggle('faq-accordeon__item_active');
    }
  };

  return (
    <section className="faq" id="faq">
      <div className="faq-wrapper">
        <div className="container">
          <h1 className="title title_size-l faq-title">FAQ</h1>
          <div className="faq-block">
            <div className="faq-accordeon">
              <ol className="faq-accordeon__list">
                {faqItems.map(({ id, title, paragraphs }) => (
                  <li
                    className="faq-accordeon__item"
                    onClick={(evt) => handleAccordeonClick(evt)}
                    key={id}
                  >
                    <div className="faq-accordeon__item-btn">
                      <span>+</span>
                      <span>-</span>
                    </div>
                    <h4 className="title title_item-l faq-accordeon__item-title">
                      {`${id}. ${title}`}
                    </h4>
                    {paragraphs.map((html) => (<p className="faq-accordeon__item-text" key={html} dangerouslySetInnerHTML={{ __html: html }} />))}
                  </li>
                ))}
              </ol>
            </div>
            <div className="faq-img">
              <img src={lady1} srcSet={`${lady1} 1x, ${lady1_2x} 2x`} alt="" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Faq;
