import React from 'react';
import rock from '../../assets/images/rock-el.svg';
import nft_calendar from '../../assets/images/nft-calendar-transparent.png';

function Terms() {
  return (
    <section className="terms">
      <div className="terms-wrapper">
        <img data-img="rock-1" src={rock} alt="" />
        <div className="container">
          <h1 className="title title_size-l terms-title">
            Terms and&nbsp;conditions
          </h1>
          <div className="terms-block">
            <div className="terms-block__first">
              <p>
                Apes of Space is a collection of digital artworks (NFTs) running
                on the Ethereum network. This website is only an interface
                allowing participants to exchange digital collectibles. Users
                are entirely responsible for the safety and management of their
                own private Ethereum wallets and validating all transactions and
                contracts generated by this website before approval.
                Furthermore, as the Apes of Space smart contract runs on the
                Ethereum network, there is no ability to undo, reverse, or
                restore any transactions.
              </p>
              <p>
                This website and its connected services are provided “as is” and
                “as available” without a warranty of any kind. By using this
                website you are accepting sole responsibility for any and all
                transactions involving Apes of Space digital collectibles.
              </p>
              <p>
                i. You Own the NFT. Each Apes of Space is an NFT on the Ethereum
                blockchain. When you purchase an NFT, you own the underlying
                Apes of Space, the Art, completely. Ownership of the NFT is
                mediated entirely by the Smart Contract and the Ethereum
                Network: at no point may we seize, freeze, or otherwise modify
                the ownership of any Apes of Space.
              </p>
            </div>
            <div className="terms-block__second">
              <p>
                Commercial Use. Subject to your continued compliance with these
                Terms grants you an unlimited, worldwide license to use, copy,
                and display the purchased Art for the purpose of creating
                derivative works based upon the Art (“Commercial Use”).
              </p>
              <p>These Terms are Subject to Change</p>
              <p>
                We may revise these Terms from time to time, but the most
                current version will always be on apesofspace.com. You are
                responsible for reviewing and becoming familiar with any such
                modifications. Revisions deemed material, in the sole discretion
                of Apes of Space, will include a notification via Twitter or
                Discord. By continuing to access the Apes of Space website and
                by holding an Apes of Space NFT, you agree to be bound by the
                current Terms
              </p>
            </div>
          </div>
          <div className="terms-calendar-wrapper">
            <a
              href="https://nftcalendar.io/event/apes-of-space-vx/"
              className="terms-calendar"
            >
              <span>As seen on</span>
              <img
                width="120"
                height="120"
                src={nft_calendar}
                alt="nft calendar"
              />
            </a>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Terms;
