/* eslint-disable */

import Web3 from "web3";
import { aosAbi, abi, aosContract, mainnetContract } from "../constants/contract";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import CoinbaseWalletSDK from "@coinbase/wallet-sdk";

// web3-modal config
const providerOptions = {
  walletconnect: {
    package: WalletConnectProvider,
    options: {
      appName: "Apes of Space",
      infuraId: "2d4f413e764740ef89d5596033efec3f",
    }
  },
  walletlink: {
    package: CoinbaseWalletSDK, 
    options: {
      appName: "Apes of Space",
      infuraId: "2d4f413e764740ef89d5596033efec3f"
    }
  },
};

let web3Modal, web3, provider;

export const connectWallet = async () => {
  web3Modal = new Web3Modal({
    network: "mainnet",
    cacheProvider: false, // optional
    providerOptions, // required
    disableInjectedProvider: false, // optional. For MetaMask / Brave / Opera.
    theme: {
      background: "#0f1315",
      main: "rgb(199, 199, 199)",
      secondary: "rgb(136, 136, 136)",
      border: "1px solid #10fdff",
      hover: "rgb(16, 26, 33)",
      borderRadius: "2px"
    }
  });

  provider = await web3Modal.connect();

  // check ethereum network
  // if (provider.chainId !== 1) {
  //   checkNetwork(provider)
  // }

  provider.on("disconnect", async (code, reason) => {
    await disconnectWallet();
  });

  web3 = new Web3(provider);
  const acc = await getAccounts()

  return acc[0]
}

export const disconnectWallet = async () => {
  await provider.close()

  await web3Modal.clearCachedProvider();
  provider = null;
}

export const getAccounts = async () => {
  try {
    const acc = await web3.eth.getAccounts()
    return acc

  } catch (e) {
    return []
  }
}

export const getMinted = async () => {
  const contract = new web3.eth.Contract(abi, mainnetContract);
  const supply = await contract.methods.totalSupply().call();

  return supply;
}

const checkNetwork = async () => {
  if (window.ethereum) {
    try {
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: '0x1' }]
      })
      return true
    } catch (error) {
      console.error(error)
      return false
    }
  } else {
    console.error("Can't setup the network on metamask because window.ethereum is undefined")
    return false
  }
}

const wlPrice = 0.08;

export const wlApes = async (address, hexProof) => {
  const contract = new web3.eth.Contract(abi, mainnetContract);

  const isWalletConnect = localStorage.getItem('walletconnect')
  let val;

  if (isWalletConnect) {
    val = web3.utils.toWei((wlPrice).toFixed(3).toString(), 'ether')
  } else {
    val = parseInt(web3.utils.toWei((wlPrice).toFixed(3).toString(), "ether")).toString(16)
  }
  const transactionParameters = {
    to: mainnetContract,
    from: address,
    value: val,
    data: contract.methods.whitelistApes(hexProof).encodeABI(),
  }

  try {
    await provider.request({
      method: "eth_sendTransaction",
      params: [transactionParameters],
    });
    return true
  } catch (error) {
    console.log(error);
  }
}