/* eslint-disable */

import React, { useRef } from 'react';
import { useWindowScroll } from 'beautiful-react-hooks';

import up_icon from '../../assets/images/icons/up-icon.svg'; 
import HomeMain from '../../components/home-main';
import Space from '../../components/space';
import Sandbox from '../../components/sandbox';
import Features from '../../components/features';
import AboutUs from '../../components/about-us';
import LadyApes from '../../components/lady-apes/lady-apes';
import About from '../../components/about';
import RoadMap from '../../components/roadMap';
import Faq from '../../components/faq';
import Tokenomics from '../../components/tokenomics';
import Team from '../../components/team';
import Investors from '../../components/investors';
import Terms from '../../components/terms';
import Footer from '../../components/footer';

const showUpBtn = (upBtn) => {
  const isBtnHidden = upBtn.classList.contains('up-btn_hidden');

  if (window.pageYOffset > 2400 && isBtnHidden) {
    upBtn.classList.remove('up-btn_hidden');
  }

  if (window.pageYOffset <= 2400 && !isBtnHidden) {
    upBtn.classList.add('up-btn_hidden');
  }
};

function Home() {
  const handleUpBtn = () => {
		topElementRef.current.scrollIntoView();
  };

	const blurRef = useRef(null);
  const upBtnRef = useRef(null);
	const topElementRef = useRef(null);

	useWindowScroll(() => {
		const upBtn = upBtnRef.current;
		showUpBtn(upBtn);
	})

  return (
		<div className="home">
			<HomeMain ref={topElementRef}/>
			<Space />
			<Sandbox />
			<Features />
			<LadyApes />
			<About />
			<RoadMap />
			<AboutUs />
			<Investors />
			<Faq />
			<Tokenomics ref={blurRef} />
			<Team />
			<Terms />
			<Footer />
			<button type="button" ref={upBtnRef} onClick={handleUpBtn} className="up-btn up-btn_hidden">
				<img src={up_icon} alt=""/>
			</button>
			<div ref={blurRef} className="back-blur" />
		</div>
  );
}

export default Home;
