import React from 'react';
import scroll from '../../assets/images/icons/scroll.svg';
import bg_full from '../../assets/images/BG-full.svg';

function FirstScreen() {
  return (
    <section data-aos="fade-down" className="first-screen" id="firstScreen">
      <div className="first-screen__wrapper">
        <img data-img="bg" src={bg_full} alt="background" />
        <div className="container">
          <h1 className="title title_size-l first-screen__title">Apes of Space VX</h1>
          <h2 className="title title_size-m first-screen__subtitle">In Metaverse - Q1 2022</h2>
          <div className="first-screen__note">
            <p>
              Apes Of Space are exploring their new home inside The Sandbox metaverse!
              NFT and The Sandbox avatar with access to premium in-game experiences,
              AOS token drop and Play2Earn benefits.
            </p>
          </div>
          <div className="first-screen__claim">
            <button type="button" className="btn btn_yellow first-screen__claim-btn">
              Apes of Space VX Soon
            </button>
            {/* <a href="/whitelist" className="btn btn_yellow first-screen__claim-btn">
              Mint Apes of Space VX
            </a> */}
          </div>
          <div className="first-screen__scroll-wrapper">
            <div className="first-screen__scroll">
              <img src={scroll} alt="" />
              <span>Scroll to see more</span>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default FirstScreen;
