import React from 'react';
import bg from '../../assets/images/BG-full.svg';
import red from '../../assets/images/red-el.svg';
import green from '../../assets/images/green-el.svg';
import rock from '../../assets/images/rock-el.svg';
import blue from '../../assets/images/blue-el.svg';
import crystal from '../../assets/images/crystal-el.svg';
import dark from '../../assets/images/dark-el.svg';
import purple from '../../assets/images/purple-el.svg';
import road from '../../assets/images/road.svg';
import road_1440 from '../../assets/images/road-1440.svg';
import road_1200 from '../../assets/images/road-1200.svg';

const roadmapItems = [
  {
    id: 1,
    reversed: true,
    img: red,
    align: 'left',
    date: 'October 2021',
    subtitle: 'Merch Store Launch and Charity',
    text: 'Show off your apes outdoors and donate to a great cause too! 40% of all Merch sales will be donated to Ape Action Africa Ape Action Africa is dedicated to the conservation of endangered gorillas and chimpanzees',
  },
  {
    id: 2,
    reversed: false,
    img: green,
    align: 'center',
    date: 'December 2021',
    subtitle: 'New NFT collection - Introducing Female Apes',
    text: 'All Apes of Space holders can claim Lady Apes Of Space NFT within a window of one month.',
  },
  {
    id: 3,
    reversed: false,
    img: rock,
    align: 'right',
    date: 'January 2022',
    subtitle: 'Public release of the Apes of Space game whitepaper for The Sandbox',
    text: 'Detailed description of Apes of Space The Sandbox game experience and design document.',
  },
  {
    id: 4,
    reversed: false,
    img: blue,
    align: 'center',
    date: 'Q1 2022',
    subtitle: 'Apes of Space VX launch playable voxel-based avatars for the metaverse.',
    text: 'Announcing our very own token, $AoS, the currency for our blockchain game, with a focus on rewards for holders! Allowing holders to accumulate and claim $AoS for holding onto their precious Apes.',
  },
  {
    id: 5,
    reversed: true,
    img: crystal,
    align: 'center',
    date: 'April 2022',
    subtitle: 'Beta Release of the Apes of Space Play-to-Earn game',
    text: 'Step into the Metaverse! Your chance to join the launch of a new era of gameplay is fast approaching.',
  },
  {
    id: 6,
    reversed: false,
    img: dark,
    align: 'center',
    date: 'June 2022',
    subtitle: 'Starting a family — Introducing Baby Apes!',
    text: 'Our Original & Female Apes have been hanging out together for a while now, so it\'s time to introduce a new family member. But there\'s a catch! In order to claim each special Baby Ape, holders must burn both an Original & Female ape. The rarity of Baby Apes will be determined by the rarity traits of both parents.',
  },
  {
    id: 7,
    reversed: false,
    img: purple,
    align: 'left',
    date: 'August 2022',
    subtitle: 'Final Release of the Apes of Space Play-to-Earn game',
    text: 'Our long-term strategy is to develop 36 LANDs into an amazing estate, with tons of different games and experiences that are fun, rewarding, and engaging, while enriching and expanding the brand of Apes of Space NFT.',
  },
];

function RoadMap() {
  return (
    <section className="road-map" id="roadmap" data-aos="fade-up">
      <div className="road-map__wrapper">
        <img data-img="bg" src={bg} alt="" />
        <div className="road-map__dark" />
        <div className="container">
          <h1 className="title title_size-l road-map__title">Roadmap</h1>
          <div className="road-map__road-wrapper">
            {roadmapItems.map((item) => {
              const flexAlignProperties = {
                left: 'flex-start',
                center: 'center',
                right: 'flex-end',
              };

              let imgOrder = item.reversed ? 1 : 0;
              let flexAlign = flexAlignProperties[item.align];
              let { align } = item;

              if (window.innerWidth <= 1200) {
                flexAlign = 'flex-start';
                align = 'left';
                imgOrder = 0;
              }

              return (
                <div
                  data-item={item.id}
                  className="road-map__item"
                  key={item.id}
                  style={{ alignItems: flexAlign, textAlign: align }}
                >
                  <div className="road-map__item-img" style={{ order: imgOrder }}>
                    <img src={item.img} alt="" />
                  </div>
                  <h4 className="title title_item-l road-map__item-title">{`${item.id}. ${item.date}`}</h4>
                  <p>{item.subtitle}</p>
                  <p>{item.text}</p>
                </div>
              );
            })}
            <div className="road-map__road">
              <img data-img="road" src={road} alt="" />
              <img data-img="road-1440" src={road_1440} alt="" />
              <img data-img="road-1200" src={road_1200} alt="" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default RoadMap;
