import React, { useState, useEffect, useRef } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Navigation } from 'swiper';
import 'swiper/swiper-bundle.min.css';
import { useWindowResize } from 'beautiful-react-hooks';

import lady1 from '../../assets/images/lady-1.png';
import lady2 from '../../assets/images/lady-2.png';
import lady3 from '../../assets/images/lady-3.png';
import lady4 from '../../assets/images/lady-4.png';
import lady5 from '../../assets/images/lady-5.png';
import lady1_2x from '../../assets/images/lady-1@2x.png';
import lady2_2x from '../../assets/images/lady-2@2x.png';
import lady3_2x from '../../assets/images/lady-3@2x.png';
import lady4_2x from '../../assets/images/lady-4@2x.png';
import lady5_2x from '../../assets/images/lady-5@2x.png';

import rock from '../../assets/images/rock-el.svg';
import red from '../../assets/images/red-el.svg';
import yellow from '../../assets/images/yellow-el.svg';
import dark from '../../assets/images/dark-el.svg';

SwiperCore.use([Navigation]);

const ladySlides = [
  { '1x': lady1, '2x': lady1_2x, id: 1 },
  { '1x': lady2, '2x': lady2_2x, id: 2 },
  { '1x': lady3, '2x': lady3_2x, id: 3 },
  { '1x': lady4, '2x': lady4_2x, id: 4 },
  { '1x': lady5, '2x': lady5_2x, id: 5 },
];

const calculateSwiper = (swiperCore) => {
  if (!swiperCore) {
    return;
  }

  const slidesArr = swiperCore.slides;
  const { activeIndex } = swiperCore;

  const activeSlide = slidesArr[activeIndex];

  const [leftSlide, prevSlide, nextSlide, rightSlide] = [
    slidesArr[activeIndex - 2],
    slidesArr[activeIndex - 1],
    slidesArr[activeIndex + 1],
    slidesArr[activeIndex + 2],
  ];

  slidesArr.forEach((slide) => {
    if (!slide.classList.contains('swiper-slide-active' || 'swiper-slide-prev' || 'swiper-slide-next')) {
      slide.style.opacity = 0;
    }
  });

  if (window.innerWidth > 991) {
    leftSlide.style.opacity = 1;
    leftSlide.style.zIndex = 1;
    rightSlide.style.opacity = 1;
    rightSlide.style.zIndex = 1;

    prevSlide.style.opacity = 1;
    prevSlide.style.zIndex = 2;
    nextSlide.style.opacity = 1;
    nextSlide.style.zIndex = 2;

    activeSlide.style.opacity = 1;
    activeSlide.style.zIndex = 3;
  } else if (window.innerWidth > 576) {
    prevSlide.style.opacity = 1;
    prevSlide.style.zIndex = 2;
    nextSlide.style.opacity = 1;
    nextSlide.style.zIndex = 2;

    activeSlide.style.opacity = 1;
    activeSlide.style.zIndex = 3;
  } else {
    slidesArr.forEach((slide) => { slide.style.opacity = 1; });
  }
};

function LadyApes() {
  const [swiperCore, setSwiperCore] = useState(null);

  const swiperRef = useRef(null);

  useEffect(() => {
    calculateSwiper(swiperCore);
  }, [swiperCore]);

  useWindowResize(() => {
    if (swiperCore) {
      calculateSwiper(swiperCore);
    }
  });

  return (
    <section className="lady-apes" id="lady">
      <div className="lady-apes__wrapper">
        <img data-img="rock-1" src={rock} alt="" />
        <img data-img="red-1" src={red} alt="" />
        <img data-img="yellow-1" src={yellow} alt="" />
        <img data-img="dark-1" src={dark} alt="" />
        <h1 data-aos="zoom-in-up" className="title title_size-l lady-apes__title">
          Lady Apes
        </h1>
        <Swiper
          slidesPerView="auto"
          onSwiper={(swiper) => {
            setSwiperCore(swiper);
          }}
          onSlideChange={(core) => calculateSwiper(core)}
          navigation={{
            prevEl: '.lady-apes__prev',
            nextEl: '.lady-apes__next',
          }}
          initialSlide={2}
          centeredSlides
          loop
          spaceBetween={50}
          ref={swiperRef}
          breakpoints={{
            577: {
              spaceBetween: 0,
            },
            470: {
              spaceBetween: 100,
            },
          }}
        >
          {ladySlides.map((slide) => (
            <SwiperSlide className="lady-apes__slide" key={slide.id}>
              <div className="lady-apes__item">
                <div className="lady-apes__item-shadow" />
                <img src={slide['1x']} srcSet={`${slide['1x']} 1x, ${slide['2x']} 2x`} alt="" />
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
        <div className="container">
          <div className="lady-apes__navigation">
            <button type="button" data-btn="prev" className="lady-apes__prev">
              <svg
                width="123"
                height="12"
                viewBox="0 0 123 12"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M0.469673 5.46967C0.176773 5.76256 0.176773 6.23744 0.469673 6.53033L5.24264 11.3033C5.53554 11.5962 6.01041 11.5962 6.3033 11.3033C6.59619 11.0104 6.59619 10.5355 6.3033 10.2426L2.06066 6L6.3033 1.75736C6.59619 1.46447 6.59619 0.989593 6.3033 0.696699C6.01041 0.403806 5.53554 0.403806 5.24264 0.696699L0.469673 5.46967ZM123 5.25L1 5.25V6.75L123 6.75V5.25Z"
                  fill="white"
                />
              </svg>
            </button>
            <button type="button" data-btn="next" className="lady-apes__next">
              <svg
                width="123"
                height="12"
                viewBox="0 0 123 12"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M122.53 6.53032C122.823 6.23743 122.823 5.76255 122.53 5.46966L117.757 0.696689C117.464 0.403795 116.99 0.403796 116.697 0.696689C116.404 0.989582 116.404 1.46446 116.697 1.75735L120.939 5.99999L116.697 10.2426C116.404 10.5355 116.404 11.0104 116.697 11.3033C116.99 11.5962 117.464 11.5962 117.757 11.3033L122.53 6.53032ZM6.55671e-08 6.75L122 6.74999L122 5.24999L-6.55671e-08 5.25L6.55671e-08 6.75Z"
                  fill="white"
                />
              </svg>
            </button>
          </div>
        </div>
      </div>
    </section>
  );
}

export default LadyApes;
