import React from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import blue_crystall from '../../assets/images/blue-crystall-el.svg';
import red from '../../assets/images/red-el.svg';
import aboutImage from '../../assets/images/ape3-4.png';
import aboutImage_2x from '../../assets/images/ape3-4@2x.png';

function AboutUs() {
  return (
    <section className="about-us">
      <div className="about-us__wrapper">
        <img data-img="blue-1" src={blue_crystall} alt="" />
        <img data-img="red-1" src={red} alt="" />
        <div className="container">
          <h1 data-aos="zoom-in-up" className="title title_size-l about-us__title">
            Apes Of Space Story
          </h1>
          <div data-aos="zoom-in-up" className="about-us__block">
            <div className="about-us__block-img">
              <LazyLoadImage src={aboutImage} srcSet={`${aboutImage} 1x, ${aboutImage_2x} 2x`} alt="" />
            </div>
            <div className="about-us__block-text">
              <h4 className="title title_item-l about-us__block-title">
                WELCOME TO THE APES OF SPACE WORLD
              </h4>
              <p>
                The history of Apes of Space begins with an event known as &quot;The Rise&quot; that
                happened some time ago (probably several generations). As a result of &quot;The Rise&quot;
                Apes were given intelligence (they are no different from us humans anymore).
              </p>
              <p>
                The initiator of the event was &quot;God the Teacher&quot;, the only human being
                on the Space Station.
              </p>
              <p>
                Space Station - central hub of the Apes of Space world, and also at the same time home
                of God the Teacher and a temple for Apes. By this reason no wars are waged on the Station
                and no weapons are used, this is a peaceful neutral territory.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default AboutUs;
