import React from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Header from '../header';
import FirstScreen from '../first-screen';
import pixelMonkey from '../../assets/images/Monkey.png';
import pixelMonkey_2x from '../../assets/images/Monkey@2x.png';
import monkeys from '../../assets/images/Monkeys.png';
import monkeys_2x from '../../assets/images/Monkeys@2x.png';
import blackMonkey from '../../assets/images/black-monkey.png';
import blackMonkey_2x from '../../assets/images/black-monkey@2x.png';

const HomeMain = React.forwardRef((props, topElementRef) => (
  <div className="home-main__wrapper" ref={topElementRef}>
    <div className="home-main">
      <Header />
      <FirstScreen />
    </div>
    <LazyLoadImage data-img="black-monkey" src={blackMonkey} srcSet={`${blackMonkey} 1x, ${blackMonkey_2x} 2x`} alt="" />
    <LazyLoadImage data-img="monkeys" src={monkeys} srcSet={`${monkeys} 1x, ${monkeys_2x} 2x`} alt="" />
    <LazyLoadImage data-img="pixelMonkey" src={pixelMonkey} srcSet={`${pixelMonkey} 1x, ${pixelMonkey_2x} 2x`} alt="" />
  </div>
));

export default HomeMain;
