import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import team1 from '../../assets/images/1team.png';
import team2 from '../../assets/images/2team.png';
import team3 from '../../assets/images/3team.png';
import team4 from '../../assets/images/4team.png';
import team5 from '../../assets/images/5team.png';
import team6 from '../../assets/images/6team.png';
import team7 from '../../assets/images/7team.png';
import team8 from '../../assets/images/8team.gif';
import team1_2x from '../../assets/images/1team@2x.png';
import team2_2x from '../../assets/images/2team@2x.png';
import team3_2x from '../../assets/images/3team@2x.png';
import team4_2x from '../../assets/images/4team@2x.png';
import team5_2x from '../../assets/images/5team@2x.png';
import team6_2x from '../../assets/images/6team@2x.png';
import team7_2x from '../../assets/images/7team@2x.png';
import linkedin from '../../assets/images/icons/linkedin.svg';
import twitter from '../../assets/images/icons/twitter.svg';

import blue_crystall from '../../assets/images/blue-crystall-el.svg';
import red from '../../assets/images/red-el.svg';

const teamMembers = [
  {
    id: 1,
    img: { '1x': team1, '2x': team1_2x },
    position: 'Project CEO',
    icon: linkedin,
    linkText: 'Linkedin',
    link: 'https://www.linkedin.com/in/ernest-de-bruyne-4b74a4225/',
    text: 'Founder of Apes of Space',
  },
  {
    id: 2,
    img: { '1x': team2, '2x': team2_2x },
    position: 'Operations Lead',
    text: 'Team Manager. Lead Tech',
  },
  {
    id: 3,
    img: { '1x': team3, '2x': team3_2x },
    position: 'Blockchain Developer',
    text: 'NFT, GameFi and DeFi Blockchain Developer',
  },
  {
    id: 4,
    img: { '1x': team4, '2x': team4_2x },
    position: 'Lead Art Designer',
    text: 'Creator of the Original & Lady Apes NFT Collections',
  },
  {
    id: 5,
    img: { '1x': team5, '2x': team5_2x },
    position: 'VX Designer',
    text: 'Licensed The Sandbox VX Designer',
  },
  {
    id: 6,
    img: { '1x': team6, '2x': team6_2x },
    position: 'Game Designer',
    text: 'Creator of Apes of Space 3D assets for the Metaverse',
  },
  {
    id: 7,
    img: { '1x': team7, '2x': team7_2x },
    position: 'Game Developer',
    text: 'Lead The Sandbox Game Developer',
  },
  {
    id: 8,
    img: { '1x': team8 },
    position: 'Mods Team',
    icon: twitter,
    linkText: '@Hafiz',
    link: 'https://twitter.com/hafizzmaw',
    text: 'On the frontline, providing support for our community',
  },
];

function Team() {
  const manageSwiper = (swiper) => {
    if (window.innerWidth > 991 && swiper) {
      swiper.disable();
    }

    if (window.innerWidth <= 991 && swiper) {
      swiper.enable();
    }
  };

  return (
    <section className="team" id="team">
      <div className="team-wrapper">
        <img data-img="blue-1" src={blue_crystall} alt="" />
        <img data-img="red-1" src={red} alt="" />
        <div className="container">
          <h1 data-aos="zoom-in-up" className="title title_size-l team-title">
            Team
          </h1>
          <div className="team-block">
            <Swiper
              data-element="team-swiper"
              slidesPerView={1}
              spaceBetween={60}
              onSwiper={(swiper) => {
                manageSwiper(swiper);
              }}
              centeredSlides
              navigation={{
                prevEl: '.team-swiper__prev',
                nextEl: '.team-swiper__next',
              }}
              breakpoints={{
                992: {
                  slidesPerView: 4,
                  spaceBetween: 0,
                  centeredSlides: false,
                },
                577: {
                  slidesPerView: 1.12,
                  spaceBetween: 80,
                  centeredSlides: true,
                },
              }}
              onResize={(swiper) => manageSwiper(swiper)}
            >
              {teamMembers.map((member) => {
                const x2Param = `${member.img['1x']} 1x, ${member.img['2x']} 2x`;
                let isX2 = true;

                if (!member.img['2x']) {
                  isX2 = false;
                }

                return (
                  <SwiperSlide key={member.id}>
                    <div data-aos="flip-left" className="team-member">
                      <div className="team-member__img">
                        <LazyLoadImage src={member.img['1x']} srcSet={isX2 ? x2Param : ''} alt="" />
                      </div>
                      <h4 className="title title_item team-member__title">{member.position}</h4>
                      {(member.link || member.linkText) && (
                      <a href={member.link} className="team-member__social-link" target="_blank" rel="noreferrer">
                        <img src={member.icon} alt="" />
                        <span>{member.linkText}</span>
                      </a>
                      )}
                      <p>{member.text}</p>
                    </div>
                  </SwiperSlide>
                );
              })}
              <div className="team-swiper__navigation">
                <button type="button" data-btn="prev" className="team-swiper__prev">
                  <svg
                    width="123"
                    height="12"
                    viewBox="0 0 123 12"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M0.469673 5.46967C0.176773 5.76256 0.176773 6.23744 0.469673 6.53033L5.24264 11.3033C5.53554 11.5962 6.01041 11.5962 6.3033 11.3033C6.59619 11.0104 6.59619 10.5355 6.3033 10.2426L2.06066 6L6.3033 1.75736C6.59619 1.46447 6.59619 0.989593 6.3033 0.696699C6.01041 0.403806 5.53554 0.403806 5.24264 0.696699L0.469673 5.46967ZM123 5.25L1 5.25V6.75L123 6.75V5.25Z"
                      fill="white"
                    />
                  </svg>
                </button>
                <button type="button" data-btn="next" className="team-swiper__next">
                  <svg
                    width="123"
                    height="12"
                    viewBox="0 0 123 12"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M122.53 6.53032C122.823 6.23743 122.823 5.76255 122.53 5.46966L117.757 0.696689C117.464 0.403795 116.99 0.403796 116.697 0.696689C116.404 0.989582 116.404 1.46446 116.697 1.75735L120.939 5.99999L116.697 10.2426C116.404 10.5355 116.404 11.0104 116.697 11.3033C116.99 11.5962 117.464 11.5962 117.757 11.3033L122.53 6.53032ZM6.55671e-08 6.75L122 6.74999L122 5.24999L-6.55671e-08 5.25L6.55671e-08 6.75Z"
                      fill="white"
                    />
                  </svg>
                </button>
              </div>
            </Swiper>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Team;
