import React, { useRef, useLayoutEffect, useState } from 'react';
import { useWindowResize } from 'beautiful-react-hooks';

const aboutItems = [
  { title: 'Breeding', text: 'Breading features to get access to Baby Apes' },
  { title: 'Provably rare', text: 'Every Ape is unique, every Ape is loved and every Ape is family' },
  { title: 'Utility', text: 'Valuable utility in the AoS ecosystem' },
  { title: 'Unique design', text: 'Fun, quirky style - unique design traits' },
  { title: 'Discord', text: 'Access to special Discord channel' },
  { title: 'Benefits', text: 'Additional benefits via roadmap activations' },
  { title: 'Ownership', text: 'Ownership and commercial usage rights' },
  { title: 'AOS NFT', text: 'Fair launch & distribution exclusively for AoS NFT owners' },
];

// To add the same height to all feautures-item elements
const calculateMinAboutHeight = (ref, setStateCallback) => {
  const aboutBlock = ref.current;
  const aboutItemsArr = Array.from(aboutBlock.children);

  if (!aboutBlock) {
    return;
  }

  setStateCallback('auto');

  const aboutHeights = aboutItemsArr.map((item) => item.clientHeight);

  const maxAboutHeight = Math.max(...aboutHeights);

  setStateCallback(`${maxAboutHeight + 4}px`);
};

function About() {
  const [minFeatureHeight, setMinFeatureHeight] = useState('auto');

  const aboutBlockRef = useRef(null);

  useLayoutEffect(() => {
    calculateMinAboutHeight(aboutBlockRef, setMinFeatureHeight);
  }, []);

  useWindowResize(() => {
    calculateMinAboutHeight(aboutBlockRef, setMinFeatureHeight);
  });

  return (
    <section className="about" id="about">
      <div className="about-wrapper">
        <div className="container">
          <h2 className="title title_size-m about-title">Lady Apes Features</h2>
          <div className="about-block" ref={aboutBlockRef}>
            {aboutItems.map((item) => (
              <div data-aos="flip-left" className="about-block__item" key={item.title} style={{ minHeight: minFeatureHeight }}>
                <h4 className="title title_item about-block__item-title">{item.title}</h4>
                <p>{item.text}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}

export default About;
